/***********************************
*** declare global CSS variables ***
***********************************/

:root {
    --measure-half: 0.625rem;
    
    --measure-1: 1rem;
    --measure-2: 1.6rem;
    --measure-3: 2.56rem;
    --measure-4: 4.096rem;
    --measure-5: 6.5536rem;
    
    --color-bg: white;
    --color-text: #000000;

    --color-main-1: #B87BA0;
    --color-main-2: #319177;
    --color-header: #3EB489;
    --color-shadow: #481935;

    --color-main-p: #CB67A3;
}

/**************************
*** some global styling ***
**************************/

html {
    height: 100%;
    width: 100%;

    overflow-x: hidden;

    margin: 0;
}

body {
    height: 100%;
    width: 100%;

    overflow-x: hidden;
    overflow-y: scroll;

    margin: 0;

    font-family: 'Barlow Semi Condensed', sans-serif;
    color: var(--color-text);
}

a {
    color: inherit;
}

img {
    max-width: 100%;
}

/*************************************/

article {
    min-width: 100%;
    min-height: 100%;
    margin: 0;

    display: flex;
    align-items: stretch;
}

article > div {
    min-width: 100%;
    min-height: 100%;
    margin: 0;

    display: flex;
    flex-direction: column;
}

#left_background {
    position: absolute;
    top: 0%;
    height: 100%;
    width: 100%;

    background: linear-gradient(135deg, var(--color-main-1) 0 calc((25vh + 25vw) * 1.414), rgba(0,0,0,0) calc((25vh + 25vw) * 1.414)) no-repeat;
    background-attachment: scroll;

    overflow: hidden;
    z-index: -3;
}

#right_background {
    position: absolute;
    top: 0%;
    height: 100%;
    width: 100%;

    background: linear-gradient(135deg, rgba(0,0,0,0) 0 calc((25vh + 25vw) * 1.414), var(--color-main-2) calc((25vh + 25vw) * 1.414)) no-repeat;
    background-attachment: scroll;

    overflow: hidden;
    z-index: -3;
}


/*******************************************/

#content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    margin: 0 var(--measure-2) var(--measure-2) var(--measure-2);
}
