#header {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    overflow: visible;

    margin: var(--measure-2) var(--measure-2) 0 var(--measure-2);
}

h1 {
    flex: 1 1 auto;

    font-family: 'Lato', sans-serif;
    font-size: var(--measure-4);
    margin: 0;

    display: flex;
    align-items: center;
}

#header h1 span {
    background-color: var(--color-main-1);
    margin-left: var(--measure-2);
}