.photo {
    flex: 1 1 40%;

    min-width: 20rem;
    max-width: 35rem;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    
    margin: var(--measure-1);
}

.photo img {
    max-height: 100%;
    max-width: 100%;
    box-shadow: var(--measure-half) var(--measure-half) var(--color-shadow);
}